<template>
  <div>
    <!-- 课程内容 -->
    <el-card class="box-card">
      <div>
        {{ this.chapter.content }}
      </div>
    </el-card>
    <!-- 添加题目 -->
    <el-button type="primary" @click="addTFQuestionDialogVisible = true"
      >添加一道判断题</el-button
    >
    <el-dialog
      title="添加一道判断题"
      :visible.sync="addTFQuestionDialogVisible"
    >
      <el-form :model="newTFQuestion">
        <el-form-item label="输入题目内容">
          <el-input
            v-model="newTFQuestion.content"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="选择答案">
          <el-select v-model="newTFQuestion.answer">
            <el-option label="对" value="true"></el-option>
            <el-option label="错" value="false"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addTFQuestionDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addTFQuestion">确 定</el-button>
      </div>
    </el-dialog>
    <el-button type="primary" @click="addMCQuestionDialogVisible = true"
      >添加一道选择题</el-button
    >
    <el-dialog
      title="添加一道选择题"
      :visible.sync="addMCQuestionDialogVisible"
    >
      <el-form :model="newMCQuestion">
        <el-form-item label="输入题目内容">
          <el-input
            v-model="newMCQuestion.content"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="输入各个选项 例: A.1 B.2 C.3">
          <el-input
            v-model="newMCQuestion.options"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="输入答案索引">
          <el-input
            v-model="newMCQuestion.answer"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addMCQuestionDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addMCQuestion">确 定</el-button>
      </div>
    </el-dialog>
    <el-button type="primary" @click="addSAQuestionDialogVisible = true"
      >添加一道简答题</el-button
    >
    <el-dialog
      title="添加一道简答题"
      :visible.sync="addSAQuestionDialogVisible"
    >
      <el-form :model="newSAQuestion">
        <el-form-item label="输入题目内容">
          <el-input
            v-model="newSAQuestion.content"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="输入参考答案">
          <el-input
            type="textarea"
            v-model="newSAQuestion.answer"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addSAQuestionDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addSAQuestion">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 题目的遍历输出 -->
    <div class="containerOfComponents" v-for="(question, index) in questions" :key="index">
      <component
        :is="getQuestionComponent(question.type)"
        :question="question"
      ></component>
      <br>
    </div>
  </div>
</template>
  
  <script>
import TrueFalseQuestion from "./true_false_question.vue";
import MultipleChoiceQuestion from "./multiple_choice_question.vue";
import ShortAnswerQuestion from "./short_answer_question.vue";
import axios from "axios";

export default {
  components: {
    TrueFalseQuestion,
    MultipleChoiceQuestion,
    ShortAnswerQuestion,
  },
  props: {
    chapter: Object,
  },
  data() {
    return {
      addTFQuestionDialogVisible: false,
      newTFQuestion: {
        chapterID: this.chapter.chapterID,
        type: 5,
        content: "",
        answer: "",
      },
      addMCQuestionDialogVisible: false,
      newMCQuestion: {
        chapterID: this.chapter.chapterID,
        type: 6,
        content: "",
        answer: "",
        options: "",
      },
      addSAQuestionDialogVisible: false,
      newSAQuestion: {
        chapterID: this.chapter.chapterID,
        type: 7,
        content: "",
        answer: "",
      },
      questions: [],
    };
  },
  methods: {
    addTFQuestion() {
      axios
        .post(
          "http://localhost:8080/SongGuo_Learning/teacher/addQuestionToChapter?questionType=5",
          this.newTFQuestion,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          if (resp.data.message == 'ADD_OK') {
            this.$notify.success({
              title: '添加成功',
              message: '您已成功添加一道判断题!'
            });
            this.addTFQuestionDialogVisible = false;
          }
        });
    },
    addMCQuestion() {
      axios
        .post(
          "http://localhost:8080/SongGuo_Learning/teacher/addQuestionToChapter?questionType=6",
          this.newMCQuestion,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          if (resp.data.message == 'ADD_OK') {
            this.$notify.success({
              title: '添加成功',
              message: '您已成功添加一道选择题!'
            });
            this.addMCQuestionDialogVisible = false;
          }
        });
    },
    addSAQuestion() {
      axios
        .post(
          "http://localhost:8080/SongGuo_Learning/teacher/addQuestionToChapter?questionType=7",
          this.newSAQuestion,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          if (resp.data.message == 'ADD_OK') {
            this.$notify.success({
              title: '添加成功',
              message: '您已成功添加一道简答题!'
            });
            this.addSAQuestionDialogVisible = false;
          }
        });
    },
    getQuestionComponent(type) {
      switch (type) {
        case 5:
          return "TrueFalseQuestion";
        case 6:
          return "MultipleChoiceQuestion";
        case 7:
          return "ShortAnswerQuestion";

        default:
          return "div";
      }
    },
  },
  mounted() {
    axios
      .get(
        "http://localhost:8080/SongGuo_Learning/teacher/getChapterQuestions?chapterID=" +
          this.chapter.chapterID,
        {
          headers: {
            Token: localStorage.getItem("token"),
          },
        }
      )
      .then((resp) => {
        console.log(resp.data);
        this.questions = resp.data.data;
        console.log(this.questions);
      });
  },
};
</script>
<style scoped>
.containerOfComponents {
  display: flex;
  flex-direction: column; /* 竖向布局 */
}
</style>
  