<template>
  <div>
    <el-page-header @back="goBack" content="课程编辑界面"> </el-page-header>
    <el-descriptions title="课程信息">
      <el-descriptions-item label="课程ID">{{
        this.course.courseID
      }}</el-descriptions-item>
      <el-descriptions-item label="课程名字">{{
        this.course.courseName
      }}</el-descriptions-item>
      <el-descriptions-item label="课程描述">{{
        this.course.description
      }}</el-descriptions-item>
    </el-descriptions>

    <el-button @click="dialogFormVisible = true">增加新课程章节</el-button>
    <el-dialog title="增加课程章节" :visible.sync="dialogFormVisible">
      <el-form :model="newChapter">
        <el-form-item label="章节名字">
          <el-input
            v-model="newChapter.chapterName"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="章节内容">
          <el-input
            type="textarea"
            v-model="newChapter.content"
            placeholder="请输入章节内容"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm">确 定</el-button>
      </div>
    </el-dialog>
    <br />
    <div
      class="containerOfComponents"
      v-for="(chapter, index) in chapters"
      :key="index"
    >
      章节名: {{ chapter.chapterName }} 章节ID: {{ chapter.chapterID }}
      <el-button
        class="deleteButton"
        @click="deleteChapter(chapter.chapterID)"
        type="text"
        >删除章节</el-button
      >
      <el-switch
        v-model="componentVisibility[index]"
        active-color="#13ce66"
        inactive-color="#ff4949"
      ></el-switch>

      <Show_chapter_info :chapter="chapter" v-if="componentVisibility[index]" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Show_chapter_info from "../question_for_teacher_edit/show_chapter_info.vue";
export default {
  components: {
    Show_chapter_info,
  },
  data() {
    let course = this.$route.query;
    let courseID = course.courseID;
    return {
      componentVisibility: [],

      dialogFormVisible: false,

      course: course,
      courseID: courseID,

      chapters: [],

      newChapter: {
        courseID: courseID,
        chapterName: "",
        content: "",
      },
    };
  },
  methods: {
    goBack() {
      this.$router.push("/teacher_homepage_personal_info");
    },
    deleteChapter(chapterID) {
      this.$confirm("确认删除章节?")
        .then(() => {
          axios
            .get(
              "http://localhost:8080/SongGuo_Learning/teacher/deleteChapter?chapterID=" +
                chapterID,
              {
                headers: {
                  Token: localStorage.getItem("token"),
                },
              }
            )
            .then((resp) => {
              console.log(resp.data);
              if (resp.data.message == "DELETE_OK") {
                this.$notify.success({
                  title: "操作成功",
                  message: "您已成功删除一个章节!",
                });
              }
            });
        })
        .catch(() => {});
    },
    submitForm() {
      axios
        .post(
          "http://localhost:8080/SongGuo_Learning/teacher/addNewChapterToCourse",
          this.newChapter,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          if (resp.data.message == "ADD_OK") {
            this.$notify.success({
              title: "添加章节成功",
              message: "您已成功添加一个课程章节!",
            });
            this.dialogFormVisible = false;
          }
        });
    },
  },
  mounted() {
    axios
      .get(
        "http://localhost:8080/SongGuo_Learning/teacher/selectCourseChaptersByCourseID?courseID=" +
          this.courseID,
        {
          headers: {
            Token: localStorage.getItem("token"),
          },
        }
      )
      .then((resp) => {
        if (resp.data.message == "TOKEN_IS_NULL") {
          this.$router.push("/studentlogin");
          return;
        }
        console.log(resp.data);

        this.chapters = resp.data.data;
      });
  },
};
</script>

<style scoped>
.containerOfComponents {
  display: flex;
  flex-direction: column; /* 竖向布局 */
}

.deleteButton {
  text-align: left
}
</style>