<template>
  <div>
    <el-col :span="8">
      <el-card shadow="hover">
        <div slot="header">选择题</div>
        <p>内容:{{ question.content }}</p>
        <p>选项:{{ question.options }}</p>
        <p>答案:{{ question.answer }}</p>
        <el-button @click="deleteQuestion(question.questionID)" type="warning">删除题目</el-button>
      </el-card>
    </el-col>
    <br>
  </div>
</template>
  
  <script>
import axios from 'axios';
export default {
  props: {
    question: Object,
  },
  data() {
    return {
      selectedOption: null,
    };
  },
  methods: {
    deleteQuestion(questionID) {
      this.$confirm("确认删除题目?")
        .then(() => {
          axios
            .get(
              "http://localhost:8080/SongGuo_Learning/teacher/deleteQuestion?questionID=" +
                questionID,
              {
                headers: {
                  Token: localStorage.getItem("token"),
                },
              }
            )
            .then((resp) => {
              console.log(resp.data);
              if (resp.data.message == 'DELETE_OK') {
                this.$notify.success({
                  title: "操作成功",
                  message: "您已成功删除一道题目!"
                })
              }
            });
        })
        .catch(() => {});
    }
  }
};
</script>
  